import btnComponent from '@/components/btnComponent.vue'
export default {
	components: {
		btnComponent
	},
	data() {
		return {
			show: false
		};
	},
	props: {
		title: {
			default: '提示'
		},
		headimg: {
			default: "/static/image/guild/hart.png"
		},
	},
	created() {
		this.show = true
	},
	mounted() {
	},
	methods: {
		closeModal() {
			this.show = false
			setTimeout(() => {
				this.$emit('closeModal')
			}, 200)
		}
	}
}
